import React from 'react'
import '../assets/css/0_basic.scss'
import Vestebro_b from '../assets/img/exhibit_program/Vestebro_b.png'
import Vestebro_l from '../assets/img/exhibit_program/Vestebro_l.png'
import Vestebro_p from '../assets/img/exhibit_program/Vestebro_p.png'
import { Mtitle, Text, SText, LRiv, Img, Line} from '../GlobalStyle'
import { link } from './components/Link'

const Vestebro = () => {
    return (
        <>
            <Mtitle mta="right">Furniture</Mtitle>
            <Text mmt="4px" mta="right">Vestebro</Text>
            <LRiv mmt="16px">
                <Img src={Vestebro_b} mw="232px" mh="232px" mml="40px"></Img>
                <Img src={Vestebro_l} mw="20px" mh="25px" mml="20px" mmt="8px"></Img>
            </LRiv>
            <Text mmt="24px">Objet Info.</Text>
            <LRiv mmt="32px">
                {link.Vestebro_f}
                {link.Vestebro_s}
                {link.Vestebro_t}
            </LRiv>
            <SText mmt="32px" mlh="16px">' '     .  <br/>    <br/>1970    .<br/><br/>      (shell)   <br/> ,   .</SText>
            <Line mmt="31.5px" mw="288px" mas="center"></Line>
            <Text mta="center" mmt="32px">Brand Info.</Text>
            <SText mta="left" mmt="24px" mlh="16px">   ,<br/>       .<br/><br/>      ,<br/>        .<br/><br/>       <br/>        .<br/><br/>,         .</SText>
            <LRiv mmt="32px" mjc="space-between">
                <Img src={Vestebro_p} mw="96px" mh="136px"></Img>
                <SText mta="right" mmt="24px" mlh="16px">    <br/>   <br/>         .<br/><br/>"    -    "<br/>Initiatives for Positive Change - For a Better Tomorrow</SText>
            </LRiv>
            <Text mta="center" mmt="40px">Personal Colors</Text>
            <SText mmt="24px" mlh="16px">     .<br/>         !<br/>  .<br/><br/>   <br/>     .<br/><br/>      !     </SText>
            <LRiv mjc="end" mmt="24px">
                {link.Vestebrolink}
            </LRiv>
        </>
    )
}

export default Vestebro
