import React from 'react'
import '../assets/css/0_basic.scss'
import Cosis_p from '../assets/img/exhibit_program/Cosis_p.png'
import Cosis_l from '../assets/img/exhibit_program/Cosis_l.png'
import { Btitle, Mtitle, Text, SText, LRiv, Img, Line } from '../GlobalStyle'
import { link } from './components/Link'

const Cosis = () => {
    return (
        <>
            <Btitle mcolor="#00677F" mta="right">Carpet</Btitle>
            <Mtitle mcolor="#00677F" mta="right" mmt="4px">Cosis</Mtitle>
            <Img src={Cosis_l} mw="89px" mh="16px" mmt="24px"></Img>
            <Img src={Cosis_p} mw="328px" mh="136px" mmt="16px"></Img>
            <Text mmt="16px">Object Info.</Text>
            <LRiv mmt="24px">
                {link.Cosis_f}
                {link.Cosis_s}
            </LRiv>
            <Line mmt="31.5px" mw="216px" mas="center"></Line>
            <Text mta="center" mmt="32px">Brand Info.</Text>
            <SText mmt="24px" mlh="16px">   ,
            <br/>LVT      
            <br/>
            <br/>2000   20     
            <br/>     ,, ,  
            <br/> //  , LVT,   
            <br/>  .</SText>
            <SText mmt="48px" mlh="16px">    .<br/><br/> ,  ,        .<br/>       <br/>        ,<br/>        <br/>   .<br/><br/>       <br/>     .<br/><br/>       <br/>     .<br/>         <br/>     ,<br/>         .<br/><br/>       <br/>      .<br/><br/>-    </SText>
            <Line mmt="31.5px" mw="216px" mas="center"></Line>
            <LRiv mjc="end" mmt="24px">
                {link.Cosislink}
            </LRiv>
        </>
    )
}

export default Cosis
